          var type = 2;
          var oper = -1;

          var inC = typeof(this.innerState) == 'undefined' ? -1 : this.innerState;
          var out = typeof(this.outterState) == 'undefined' ? -1 : this.outterState;
          console.log("++++++++++++" + " deviceAddress " + deviceAddress + "  type " + type + " oper " + oper + "  inC " + inC + " out " + out);
          //
          Cordova.exec(this.operateSuccessFunction, this.operateFailFunction, "SmartHomePlugins",
