        } else {
          var deviceAddress = this.isOperateDeviceAdress;
          var oper = -1;
          var status = this.state;
          console.log("++++++++++++" + " deviceAddress " + deviceAddress + " status " + status + " oper " + oper);
          //
          Cordova.exec(this.operateSuccessFunction, this.operateFailFunction, "SmartHomePlugins",
