        this.isToPop = true;
        this.curtainName = data.name;
        this.isOperateDeviceAdress = data.address;
        if (data.deviceTypeCode == 0x0009) {
          console.log("")
          this.isDoubleCurtain = false;
          //
          this.state = status.status
          //
        } else if (data.deviceTypeCode == 0x0013) {
          //
          this.isDoubleCurtain = true;
          this.innerState = status.in
          this.outterState = status.out
        }

