          item = {name: "", deviceTypeCode: 77777777}
        } else if (index == "3") {
          item = {name: '', deviceTypeCode: 0x000B};
        } else if (index == "4") {
          item = {name: '', deviceTypeCode: 88888888};
        } else if (index == "8") {
          item = {name: '', deviceTypeCode: 0x7F08};
        } else {
