<template>
  <div class="wapper">
    <div class="block"></div>
    <!---->
    <titleBar v-bind:title-name="name" v-bind:is-visible="isTitleBarBackImageVisible"></titleBar>
    <p style="font-size: large;"></p>
  </div>
</template>


<script>
  export default {
    data() {
      return {
        name:'',
        isTitleBarBackImageVisible:true,
      }
    },

  }
</script>


<style>
  html,body,wrapper{
    height:100%;
    overflow:hidden;
  }
</style>



