export default {
  /***************************/
  install(Vue, options) {
    Vue.prototype.computeImageInList = function (list) {
      //
      list.map(function (obj) {
        switch (obj.type) {
          case "0":
          case 0:
            return obj.src = "static/image/fingerprint.png"
            break;
          case "1":
          case 1:
            return obj.src = "static/image/psw.png"
            break;
          case "2":
          case 2:
            return obj.src = "static/image/key.png"
            break;
          case "4":
          case 4:
            return obj.src = "static/image/card.png"
            break;
          case "5":
          case 5:
            return obj.src = "static/image/tempPsw.png"
            break;
          default:
            return obj.src = "static/image/app.png"
            break;
        }
      })
    }

    Vue.prototype.computeImageInListInRelative = function (list) {
      console.log(JSON.stringify(list))
      //
      list.map(function (obj) {
        switch (obj.type) {
          case "0":
          case 0:
            return obj.src = "static/image/fingerprint.png"
            break;
          case "1":
          case 1:
            return obj.src = "static/image/psw.png"
            break;
          case "2":
            return obj.src = "static/image/key.png"
            break;
          case "4":
          case 4:
            return obj.src = "static/image/card.png"
            break;
          case "5":
          case 5:
            return obj.src = "static/image/tempPsw.png"
            break;
          default:
            return obj.src = "static/image/app.png"
            break;
        }
      })
    };
  },

}



