<template xmlns:v-on="http://www.w3.org/1999/xhtml" xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="wapper">
    <!---->
    <titleBar :title-name="name"  :is-visible="isTitleBarBackImageVisible" :is-save-visible="isSaveVisible"
              :is-left-click="true" :back-src="backSrc" @LeftBttonClick='backToList'></titleBar>

    <div class="contentList">
      <mu-divider/>
      <p class="hintContentList"></p>
      <mu-divider/>
      <!---->
      <div class="demo-grid">
        <!---->
        <mu-row>
          <mu-col class="controllerItem" v-for="(device,index) in devicesList" width="100" tablet="100" desktop="100">
            <div class="controller" @touchstart ="showUnBindDialog(index)" @touchend="clearLoop">
              <img class='controllerImage' src="../../../../static/image/controller_icon.png"/>
              <!--<img class='settingsImage' v-if="device.isCurrentHost"  v-on:click="clickToSettings" src="../../../../static/image/ic_settings.png"/>-->
              <div class="flexLeft">
                <p class="textName">{{device.name}}</p>
                <p class="textstatus" v-if="device.status==1"></p>
                <p class="textstatus" v-if="device.status==0"></p>
              </div>
              <div class="flexRight">
                <img class='checkImageHidden' v-bind:class="{checkImageVisible: device.isCurrentHost}"
                     src="../../../../static/image/check_icon.png"/>
                <p class="text1" v-if="device.isCurrentHost"></p>
                <p class="text1" v-else v-on:click.stop="setToCurrentController(index)"></p>
              </div>
            </div>
            <mu-divider/>
          </mu-col>
        </mu-row>
      </div>
    </div>
    <!---->
    <div class="addControllerDiv">
      <mu-divider/>
      <img class="addButton" v-on:click="clickToaddController_page"
           src="../../../../static/image/addControllerIcon.png"/>
      <div v-on:click="clickToaddController_page"></div>
    </div>


    <!---->
    <div v-if="devicesList.length!=0" class="confirmDialogInvisible"
         v-bind:class="{confirmDialogVisible:isConfirmDialogVisible}">
      <mu-dialog :open="isConfirmDialogVisible" title="" @close="close">
         {{this.devicesList[this.clickControllerIndex].name}} 
        <mu-flat-button slot="actions" @click="close" primary label=""/>
        <mu-flat-button slot="actions" primary @click="confirm" label=""/>
      </mu-dialog>
    </div>

    <!---->
    <div v-if="devicesList.length!=0">
      <mu-dialog :open="unBindDialog" title="" @close="close">
         {{this.devicesList[this.unBindDialogIndex].name}} 
        <mu-flat-button slot="actions" @click="closeUnBind" primary label=""/>
        <mu-flat-button slot="actions" primary @click="unBind" label=""/>
      </mu-dialog>
    </div>
  </div>
</template>

<script>

  import {mapMutations, mapState, mapActions} from 'vuex'

  export default {
    data() {
      return {
        name: '',
        isTitleBarBackImageVisible: true,
        isSaveVisible: false,
        backSrc: "static/image/close.png",
        //
        devicesList: [],
        //
        OriginDeviceList: [],
        //
        currentControllerIndex: 0,
        //
        isConfirmDialogVisible: false,
        //
        clickControllerIndex: 0,
        Loop: "",
        unBindDialog: false,
        unBindDialogIndex: 0,
        fromPage: this.$route.query.fromPageName,
      }
    },
    computed: {},

    mounted() {
      document.getElementsByClassName("TitleBar")[0].style.background = "#ffffff";
      document.getElementsByClassName("TitleName")[0].style.color = "black";
    },
    created() {
      if (this.isDebug) {
        //
        this.OriginDeviceList = [
          {isCurrentHost: false, deviceType: '', name: '', status: '0', mac: "111"},
          {isCurrentHost: false, deviceType: '', name: '', status: '1', mac: "222"},
          {isCurrentHost: false, deviceType: '', name: '', status: '0', mac: "333"},
          {isCurrentHost: false, deviceType: '', name: '', status: '1', mac: "444"},

        ],
        //
        this.devicesList = this.OriginDeviceList;
//        this.devicesList=[];
      } else {
        //
        Cordova.exec(this.getHostListSuccessFunction, this.getHostListFailFunction, "SmartHomePlugins",
          "getHostList", []);
      }
    },

    methods: {

      //this.xxxthis.$store.commit('xxx')`
      ...mapMutations([
        'refreshIsFromRegionPage',  //
      ]),

      ...mapActions([
        'refreshCurrentRegionObjByActions', //
      ]),

      // 
      showUnBindDialog(index) {
        this.unBindDialogIndex = index;
        clearInterval(this.Loop);//
        this.Loop = window.setInterval(this.Interval, 800);
      },
      Interval: function () {
        //
        console.log("")
        this.unBindDialog = true;
      },

      clearLoop() {
        window.clearInterval(this.Loop);
      },

      //
      unBind: function () {
        this.unBindDialog = false;
        var mac1 = this.devicesList[this.unBindDialogIndex].mac
        //
        if (this.isDebug) {
          console.log(" mac1" + mac1)
        } else {
          Cordova.exec(this.unbindHostSuccessFunction, this.unbindHostFailFunction, "SmartHomePlugins",
            "unbindHost", [mac1]);
        }

      },
      closeUnBind: function () {
        this.unBindDialog = false
      },

      unbindHostSuccessFunction: function () {
        this.showNativeToast("")
        // 
        Cordova.exec(this.getHostListSuccessFunction, this.getHostListFailFunction,"SmartHomePlugins",
          "getHostList", []);
      },

      unbindHostFailFunction: function () {
        this.showNativeToast("")
      },

      getHostListSuccessFunction: function (message) {
        var obj = eval("(" + message + ")");
        console.log("" + JSON.stringify(obj))
        //-{
//        "id": 9,
//          "name": "ut000000000000000000000000000000",
//          "mac": "000000000001",
//          "type": "host",
//          "bootVersion": "1000",
//          "configVersion": "3",
//          "dataVersion": "1000",
//          "serverVersion": "1000",
//          "qrcode": "111",
//          "createTime": 1516848110000,
//          "status": "1",
//          "isCurrentHost": 1
//      }
        obj.sort((obj)=>{
          if(obj.isCurrentHost){
            return -1;
          }else{
            return 1;
          }
        });
        this.devicesList = obj;
      },
      getHostListFailFunction: function () {
        console.log("")
        this.devicesList = [];
      },

      backToList: function () {
        console.log("fromPage  " + this.fromPage)
        if (this.fromPage == 'mine_page') {
          this.$router.push(
            {
              path: '/mine_page'
            }
          );
        } else {
          this.$router.push(
            {
              path: '/scene_page'
            }
          );
        }

      },
      //
      setToCurrentController: function (index) {
        console.log("")
        //
        this.clickControllerIndex = index;
        //
        this.isConfirmDialogVisible = true;
      },

      confirm() {
        if (this.isDebug) {
          //-1 
          this.refreshCurrentRegionObjByActions(-1);
          this.refreshIsFromRegionPage(false)
          //
          this.isConfirmDialogVisible = false;
          //
          this.devicesList[this.currentControllerIndex].isCurrentHost = false;
          //
          this.currentControllerIndex = this.clickControllerIndex;
          //
          this.devicesList[this.currentControllerIndex].isCurrentHost = true;
          this.devicesList.sort((obj)=>{
            if(obj.isCurrentHost){
              return -1;
            }else{
              return 1;
            }
          });
        } else {
          var mac1 = this.devicesList[this.clickControllerIndex].mac;
          console.log("+++++++++mac1+++++++" + mac1)
          Cordova.exec(this.setCurrentHostSuccessFunction, this.setCurrentHostFailFunction, "SmartHomePlugins",
            "setCurrentHost", [mac1]);
        }
      },

      setCurrentHostSuccessFunction: function () {
        console.log("")
        //
        this.isConfirmDialogVisible = false;
        //  
        //-1 
        this.refreshCurrentRegionObjByActions(-1);
        this.refreshIsFromRegionPage(false)

//        //
//        this.devicesList[this.currentControllerIndex].isCurrentHost=false;
//        //
//        this.currentControllerIndex = this.clickControllerIndex;
//        //
//        this.devicesList[this.currentControllerIndex].isCurrentHost=true;
//        console.log("" +this.devicesList[this.currentControllerIndex].name +"false"
//          +"" +this.devicesList[this.currentControllerIndex].name +"true")
        Cordova.exec(this.getHostListSuccessFunction, this.getHostListFailFunction, "SmartHomePlugins",
          "getHostList", []);
      },
      setCurrentHostFailFunction: function () {
        console.log("")
      },
      close() {
        //
        this.isConfirmDialogVisible = false
      },
      //
      clickToSettings: function () {
        this.$router.push(
          {
            path: '/settings'
          }
        );
      },
      //
      clickToaddController_page: function () {
        this.$router.replace(
          {
            path: '/udpConfig',
            query: {
              fromPageName: this.fromPage,
            }
          }
        );
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style>

  .hintContentList {
    font-size: 16px;
    color: black;
    opacity: 0.5;
    text-align: left;
    padding-left: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .contentList {
    margin-top: 64px;
    height: calc(100% - 64px);
    overflow: scroll;
    background-color: #f9f9f9;
  }

  .addControllerDiv {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    bottom: 0;
    position: fixed;
    background-color: white;
  }

  .addButton {
    width: 40px;
    height: 40px;
  }

  .demo-paper {
    display: inline-block;
    text-align: center;
    position: fixed;
    left: 0;
    bottom: 0;
    font-size: medium;
    flex-direction: column;
    background-color: white;
    align-items: center;
    width: 100%;
    vertical-align: middle;
  }

  .demo-grid div[class*="col-"] {
    margin-bottom: 0px;
    padding: 0px;
    -webkit-overflow-scrolling: touch;
  }

  .text1 {
    margin: 0px;
    font-size: small;
  }

  .textstatus {
    margin: 0px;
    font-size: small;
    opacity: 0.5;

  }

  .textName {
    font-size: medium;
    margin-bottom: 5px;
  }

  .settingsImage {
    position: absolute;
    top: 15%;
    left: 13%;
    width: 22px;
    height: 22px;
  }

  .flexLeft {
    flex: 1;
    text-align: left;
    /*text-align:center;*/
    vertical-align: middle;
    justify-content: center;
    flex-direction: column;
    display: flex;
  }

  .flexRight {
    flex: 1;
    justify-content: center;
    flex-direction: column;
    display: flex;
  }

  .controller {
    position: relative;
    display: flex;
    flex-direction: row;
    background-color: white;
  }

  .controllerImage {
    width: 20%;
    height: 20%;
  }

  .checkImageHidden {
    /*margin-top: 13px;*/
    width: 20%;
    height: 40%;
    visibility: hidden;
    margin: 0 auto;
  }

  .checkImageVisible {
    width: 20%;
    height: 40%;
    visibility: visible;
    margin: 0 auto;
  }

  .controllerItem {
    background-color: white;
    width: 100%;
  }

  .no_fix_height_wapper {
    background-color: lightgrey;
  }

  .hint {
    font-size: small;
    opacity: 0.7;
    height: 20px;
    margin-bottom: 0px;
    margin-left: 20px;
    text-align: left;
  }

  html, body, wrapper {
    height: 100%;
    /*overflow:hidden;*/
  }
</style>



