<template>
  <div style="height: 100%">
    <!---->
    <titleBar v-bind:title-name="name"></titleBar>
    <div class="wrapperPage">
    </div>
    <div class="bottomNav">
      <mu-divider />
      <bottomNav v-bind:bottom-index="bottomNavIndex"></bottomNav>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        name:'',
        bottomNavIndex:'',
      }
    },
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style>

  .wrapperPage{
    margin-top: 64px;
    font-size: large;
    height: calc(100%  - 64px);
  }
  /**/
  html,body,wrapper{
    height:100%;
    overflow:hidden;
  }
</style>



