<template xmlns:v-bind="" xmlns:v-on="http://www.w3.org/1999/xhtml">
  <div style="height: 100%;background-color: lightgrey">
    <!---->
    <titleBar v-bind:title-name="name"></titleBar>
    <div class="mineWrapper">


      <!---->
      <div class="accountInfo">
        <div class="titleBarHeightBlock"></div>
        <div class="userImage"></div>
        <div style="position: relative">
          <a class="phoneNumText">{{accountNum}}</a>
        </div>
        <!---->
        <!--<a class="changePwd"></a>-->

        <!---->
        <div class="regionAndcontroller">
          <p class="region" v-on:click="toRegionList">
            {{regionNum}}<br/></p>
          <p class="fenge"></p>
          <p class="controller1" v-on:click="toControllerList">
            {{hostNum}}<br/></p>
        </div>
      </div>

      <!---->
      <!--<a class="deviceTitleText"></a>-->
      <!--<div class="blueToothClass">-->
      <!--<mu-list-item title="">-->
      <!--<mu-icon  v-on:click="addBlueToothDevice"  slot="right" value=" " class="localImageUrl"/>-->
      <!--</mu-list-item>-->
      <!--</div>-->

      <!--App-->
      <a class="AppSettingTitleText"></a>
      <div style="position:relative;background-color:white">
        <mu-list-item title="App">
          <mu-badge content="new" secondary slot="after"/>
        </mu-list-item>
        <mu-divider/>
        <mu-list-item title=""></mu-list-item>
        <mu-divider/>
        <mu-list-item title=""></mu-list-item>
        <mu-divider/>
        <mu-list-item title="" @click="toAbout"></mu-list-item>
      </div>

      <div style="flex: 1;display: flex;flex-direction: column;justify-content: center;align-items: center">
        <!--<div></div>-->
        <mu-raised-button v-on:click="logOut" label="" class="logButton" primary/>
      </div>

    </div>

    <!--Tab-->
    <div class="bottomNav">
      <mu-divider/>
      <bottomNav v-bind:bottom-index="bottomNavIndex"></bottomNav>
    </div>

    <mu-toast v-if="toast" message="" @close="hideToast"/>
  </div>
</template>


<style>
  .mineWrapper {
    margin-top: 64px;
    height: calc(100% - 56px - 64px);
    overflow: scroll;
    display: flex;
    flex-direction: column;
    background-color: #f9f9f9;
  }

  /**/
  html, body, wrapper {
    height: 100%;
    overflow: hidden;
  }

  .accountInfo {
    position: relative;
    align-items: center;
    flex: 1;
    background-color: white;
    width: 100%;
    flex-direction: column;
  }

  .deviceTitleText {
    padding-left: 5%;
    text-align: left;
    width: 100%;
    font-size: 16px;
    color: grey;
  }

  .AppSettingTitleText {
    padding-left: 5%;
    text-align: left;
    width: 100%;
    font-size: 16px;
    color: grey;
    margin-top: 5px;
    margin-bottom: 5px;
  }

  .changePwd {
    color: black;
    font-size: 14px;
    margin-right: 10%;
    float: right;
  }

  .controller1 {
    flex: 1;
    padding-right: 20%;
  }

  .region {
    flex: 1;
    padding-left: 20%;
  }

  .regionAndcontroller {
    flex-direction: row;
    display: flex;
    width: 100%;
    align-items: center;
    position: absolute;
    bottom: 0;
  }

  .fenge {
    margin-left: 6px;
    padding: 12px 6px 0px 6px;
    border-left: 1px solid lightgrey;
    font-size: 0;
  }

  .userImage {
    background-image: url(../../../static/image/user_image.png);
    background-repeat: no-repeat;
    width: 48px;
    height: 48px;
    background-size: auto 48px;
    margin: 0 auto
  }

  .phoneNumText {
    color: black;
    margin: 0 auto

  }

  .blueToothClass {
    position: relative;
    background-color: white

  }

  .logButton {
    font-size: large;
    width: 90%;
    FILTER: progid: DXImageTransform . Microsoft . Gradient(gradientType = 0, startColorStr = #4cc6ff,
    endColorStr = #1eaef9); /*IE*/
    background: -moz-linear-gradient(left, #4cc6ff, 1 eaef9); /**/
    background: -webkit-gradient(linear, 0% 0%, 100% 0%, from(#4cc6ff), to(#1eaef9)); /**/
  }

  /*;*/
  /*.material-icons{*/
  /*background-image: url(../../../static/image/add_icon.png) ;*/
  /*!*background-repeat:no-repeat;*!*/
  /*!*width:30px;*!*/
  /*!*height:30px;*!*/
  /*!*background-size:auto 30px;*!*/
  /*!*background-color: red;*!*/
  /*}*/

  .blueToothClass .material-icons.localImageUrl {
    width: 30px;
    height: 30px;
    background-size: auto 30px;
    background-image: url(../../../static/image/add_icon.png)
  }

  /*;*/
  .mu-item.show-right {
    width: 100%;
    text-align: left;
  }

  .mu-item {
    text-align: left;
  }
</style>


<script>

  export default {
    components: {},

    data() {
      return {
        name: '',
        accountNum: '...',
        bottomNavIndex: '',
        regionNum: "0",
        hostNum: "0",
        toast: false,
        msg: ""
      }
    },

    //
    created() {
      if (this.isDebug) {

      } else {
        Cordova.exec(this.getUserInfoSuccess, this.getUserInfoFail, "SmartHomePlugins", "getUserInfo", []);
      }
    },
    mounted() {
      //console.log("mounted")
    },
    methods: {
      toAbout:function () {
        this.$router.push({
          path: '/about_page',
          name: 'about_page',
        })
      },
      logOut: function () {
        console.log("")
        if (this.isDebug) {
          this.$router.push({
            path: '/login',
            name: 'login',
          })
        } else {
          Cordova.exec(this.logoutSuccess, this.logoutFail, "SmartHomePlugins", "logout", []);
        }

      },

      logoutSuccess: function () {
        console.log("++++++++++++++ ")
        this.$router.push({
          path: '/login',
          name: 'login',
        })
      },
      logoutFail: function () {
        console.log("++++++++++++++ ")
      },


      getUserInfoSuccess: function (message) {
        var obj = eval("(" + message + ")");
        this.hostNum = obj.host;
        this.regionNum = obj.region;
        this.accountNum = obj.account;
        console.log(" " + " " + this.hostNum + "" + this.regionNum + " " + this.accountNum)
      },
      getUserInfoFail: function (message) {
        console.log(" ")
      },
      showToast() {
        this.toast = true;
        if (this.toastTimer) clearTimeout(this.toastTimer);
        this.toastTimer = setTimeout(() => {
          this.toast = false
        }, 1000)
      },
      hideToast() {
        this.toast = false;
        if (this.toastTimer) clearTimeout(this.toastTimer)
      },

      toControllerList: function () {
        this.$router.push({
          path: '/myControllerList',
          name: 'myControllerList',
          query: {
            fromPageName: 'mine_page',
          }
        })
      },

      toRegionList: function () {
        this.$router.push({
          path: '/myRegionList_page',
          name: 'myRegionList_page',
        })

      },

      addBlueToothDevice: function () {
        this.$router.push({
          path: '/addBlueToothDevice',
          name: 'addBlueToothDevice',
//
        })
      }

    }

  }
</script>



